export default function (sequelize, DataTypes) {
  const Address = sequelize.define('Address', {
    id: {
      type: DataTypes.INTEGER,
      allowNull: false,
      primaryKey: true,
      autoIncrement: true,
    },
  }, {
    tableName: 'shipments',
    timestamps: true,
    paranoid: true,
    classMethods: {
      associate(db) {
        Address.belongsTo(db.User, {
          foreignKey: 'userId',
          allowNull: false,
        });

        Address.hasMany(db.Shipment);
      },
    },
  });

  return Address;
}
